.tu-color-predefine {
  display: flex;
  font-size: 12px;
  margin-top: 8px;
  width: 280px; }
  .tu-color-predefine__colors {
    display: flex;
    flex: 1;
    flex-wrap: wrap; }
  .tu-color-predefine__color-selector {
    margin: 0 0 8px 8px;
    width: 20px;
    height: 20px;
    border-radius: 4px;
    cursor: pointer; }
    .tu-color-predefine__color-selector:nth-child(10n + 1) {
      margin-left: 0; }
    .tu-color-predefine__color-selector.selected {
      box-shadow: 0 0 3px 2px #6d5dfc; }
    .tu-color-predefine__color-selector > div {
      display: flex;
      height: 100%;
      border-radius: 3px; }
    .tu-color-predefine__color-selector.is-alpha {
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==); }

.tu-color-hue-slider {
  position: relative;
  box-sizing: border-box;
  width: 280px;
  height: 12px;
  background-color: #f00;
  padding: 0 2px; }
  .tu-color-hue-slider__bar {
    position: relative;
    background: linear-gradient(180deg, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
    height: 100%; }
  .tu-color-hue-slider__thumb {
    position: absolute;
    cursor: pointer;
    box-sizing: border-box;
    left: 0;
    top: 0;
    width: 4px;
    height: 100%;
    border-radius: 1px;
    background: #fff;
    border: 1px solid #f0f0f0;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
    z-index: 1; }
  .tu-color-hue-slider.is-vertical {
    width: 12px;
    height: 180px;
    padding: 2px 0; }
    .tu-color-hue-slider.is-vertical .tu-color-hue-slider__bar {
      background: linear-gradient(to bottom, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%); }
    .tu-color-hue-slider.is-vertical .tu-color-hue-slider__thumb {
      left: 0;
      top: 0;
      width: 100%;
      height: 4px; }

.tu-color-svpanel {
  position: relative;
  width: 280px;
  height: 180px; }
  .tu-color-svpanel__white, .tu-color-svpanel__black {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  .tu-color-svpanel__white {
    background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0)); }
  .tu-color-svpanel__black {
    background: linear-gradient(to top, #000, rgba(0, 0, 0, 0)); }
  .tu-color-svpanel__cursor {
    position: absolute; }
    .tu-color-svpanel__cursor > div {
      cursor: head;
      width: 4px;
      height: 4px;
      box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3), 0 0 1px 2px rgba(0, 0, 0, 0.4);
      border-radius: 50%;
      transform: translate(-2px, -2px); }

.tu-color-alpha-slider {
  position: relative;
  box-sizing: border-box;
  width: 280px;
  height: 12px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==); }
  .tu-color-alpha-slider__bar {
