    .tu-color-alpha-slider.is-vertical .tu-color-alpha-slider__bar {
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, white 100%); }
    .tu-color-alpha-slider.is-vertical .tu-color-alpha-slider__thumb {
