.tu-color-dropdown {
  margin-top: 12px;
  width: 300px;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-color-dropdown__main-wrapper {
    margin-bottom: 6px; }
    .tu-color-dropdown__main-wrapper::after {
      content: "";
      display: table;
      clear: both; }
  .tu-color-dropdown__btns {
    margin-top: 12px;
    text-align: right; }
  .tu-color-dropdown__value {
    float: left;
    font-size: 12px;
    color: #000000;
    width: 160px; }
  .tu-color-dropdown__link-btn {
    margin-right: 8px; }

.tu-color-picker {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  line-height: normal;
  height: 32px; }
  .tu-color-picker.is-disabled .tu-color-picker__trigger {
    cursor: not-allowed; }
  .tu-color-picker.is-focus .tu-color-picker__trigger {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-color-picker--large {
    height: 40px; }
    .tu-color-picker--large .tu-color-picker__trigger {
      padding: 8px;
      height: 40px;
      width: 40px; }
    .tu-color-picker--large .tu-color-picker__mask {
      height: 38px;
      width: 38px; }
  .tu-color-picker--medium {
    height: 32px; }
    .tu-color-picker--medium .tu-color-picker__trigger {
      height: 32px;
      width: 32px; }
    .tu-color-picker--medium .tu-color-picker__mask {
      height: 30px;
      width: 30px; }
  .tu-color-picker--small {
    height: 28px; }
    .tu-color-picker--small .tu-color-picker__trigger {
      padding: 8px;
      height: 28px;
      width: 28px; }
    .tu-color-picker--small .tu-color-picker__mask {
      height: 26px;
      width: 26px; }
    .tu-color-picker--small .tu-color-picker__icon,
    .tu-color-picker--small .tu-color-picker__empty {
      transform: translate3d(-50%, -50%, 0) scale(0.8); }
  .tu-color-picker--mini {
    height: 22px; }
    .tu-color-picker--mini .tu-color-picker__trigger {
      padding: 5px;
      height: 22px;
      width: 22px; }
    .tu-color-picker--mini .tu-color-picker__mask {
      height: 20px;
      width: 20px; }
    .tu-color-picker--mini .tu-color-picker__icon,
    .tu-color-picker--mini .tu-color-picker__empty {
      transform: translate3d(-50%, -50%, 0) scale(0.8); }
  .tu-color-picker__mask {
    height: 38px;
    width: 38px;
    border-radius: 4px;
    position: absolute;
    top: 1px;
    left: 1px;
    z-index: 1;
    cursor: not-allowed;
    background-color: rgba(255, 255, 255, 0.7); }
  .tu-color-picker__trigger {
    display: inline-block;
    box-sizing: border-box;
    height: 32px;
    width: 32px;
    padding: 7px;
    border-radius: 2px;
    font-size: 0;
    position: relative;
    cursor: pointer;
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-color-picker__color {
    position: relative;
    display: block;
    box-sizing: border-box;
    border-radius: 2px;
    width: 100%;
    height: 100%;
    text-align: center; }
    .tu-color-picker__color.is-alpha {
      background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==); }
  .tu-color-picker__color-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0; }
  .tu-color-picker__empty {
    font-size: 12px;
    color: #c8d0e7;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0); }
  .tu-color-picker__icon {
    display: inline-block;
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    color: #ffffff;
    text-align: center;
    font-size: 12px; }
  .tu-color-picker__panel {
    position: absolute;
    z-index: 10;
    padding: 6px;
    box-sizing: content-box;
    border: 1px solid #ebeef5;
    border-radius: 4px; }
